import React from "react";

function Features(props) {
  return (
    <div className="col-md-6" style={{ marginTop: "3%" }}>
      <img
        src={props.img}
        alt=""
        width="50px"
        height="50px"
        style={{ marginLeft: "20%" }}
      />
      <h3 style={{ marginRight: "50px", marginLeft: "20%" }}>
        {props.heading}
      </h3>
      <p style={{ marginRight: "90px", marginLeft: "20%" }}>{props.content}</p>
    </div>
  );
}

export default Features;
